import m from 'mithril'
import classNames from 'classnames'

export default class Home {
    constructor(vnode){

    }
    view(){
        return m('div',{
            class: 'main home'
        },[
            m('section.sec1',[
                m('h2',[
                    m('span',''),
                    m('br'),
                    m('span','')
                ])
            ]),
            m('section.sec2',[
                m('.sec2-heading',[
                    m('h2',[
                        m('span.h2-en','About'),
                        m('span.h2-ch','')
                    ])
                ]),
                m('.sec2-row',[
                    m('div',{
                        class: classNames('sec2-row-col','img-block')
                    },[
                        m('img',{
                            src: './images/about/Self-portrait-01.jpg',
                            alt: '',
                            style: {
                                width: '100%',
                                borderRadius: '0.5rem'
                            }
                        })
                    ]),
                    m('div',{
                        class: classNames('sec2-row-col','text-block')
                    },[
                        m('h3',''),
                        m('p',[
                            m('ul',[
                                m('li',' '),
                                m('li',' ')
                            ])
                        ]),
                        m('.sec2-row-col-btn',[
                            m('button[type=button]','MORE')
                        ])
                    ])
                ])
            ]),
            m('section.sec3',['']),
            m('section.sec4',['']),
            m('section.sec5',['']),
        ])
    }

}